___CSS_LOADER_EXPORT___.push([module.id, `.comment {
  background: #222;
  padding: 24px;
  margin-top: 32px;
  pointer-events: none;
  z-index: 1;
  position: absolute;
  opacity: 0;
  bottom: 0;
}
.comment.active {
  position: static;
  pointer-events: all;
  opacity: 1;
}
.comment .textarea__wr {
  width: 100%;
}
.comment__count {
  color: #fff;
  font-size: 24px;
  margin-bottom: 25px;
  font-weight: 500;
}
.comment__btn {
  height: fit-content;
}
.comment__add {
  display: flex;
  width: 100%;
  align-items: center;
}
.comment__comments {
  margin-top: 15px;
}
.comment__item {
  margin-bottom: 15px;
}
.comment__item_top {
  display: flex;
  align-items: center;
  gap: 15px;
}
.comment__item__avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
}
.comment__item__name {
  color: #fff;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 16px;
  font-weight: 600;
  line-height: 18px;
}
.comment__comment {
  color: #fff;
  font-feature-settings: "clig" off, "liga" off;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  margin-left: 47px;
